/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  min-height: 95%;
  display: flex;
  flex-direction: column;
  background-color: #e1e1e1;
  color: #1a1b15;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #1a1b15;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.dark-mode {
  background-color: #1a1a1a;
  /* Estilo para el cuadro de búsqueda */
  /* Estilo para el label asociado al cuadro de búsqueda */
  /* Estilo para los botones de paginación de DataTables */
  /* Estilo para el texto de la información de DataTables (ejemplo: "Showing 1 to 10 of 20 entries") */
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #e1e1e1;
}
.dark-mode blockquote {
  color: #FFFFFF;
}
.dark-mode .anuncio,
.dark-mode .resumen-propiedad {
  background-color: #333333;
  border: 2rem solid #333333;
}
.dark-mode .anuncio .precio,
.dark-mode .resumen-propiedad .precio {
  color: #329f00;
}
.dark-mode .icono {
  filter: invert(100%);
}
.dark-mode .formulario label,
.dark-mode .formulario legend,
.dark-mode .formulario p {
  color: #e1e1e1;
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario textarea,
.dark-mode .formulario select {
  background-color: #333333;
  color: #FFFFFF;
  border: 1px solid #333333;
}
.dark-mode .tabla-container {
  color: #FFFFFF;
  background-color: #333333;
}
.dark-mode table.propiedades {
  width: 100%;
  border-collapse: collapse;
  background-color: #333333;
}
.dark-mode table.propiedades td {
  background-color: #333333;
  color: #FFFFFF;
}
.dark-mode .dataTables_filter input {
  font-weight: 700 !important;
  margin-bottom: 2rem;
  color: 900 !important;
  background-color: #fff !important;
  border: 1px solid #ccc !important;
}
.dark-mode .dataTables_filter label {
  font-weight: 700 !important;
  margin-bottom: 2rem;
  color: #ffffff !important; /* Color del texto del label */
}
.dark-mode .dataTables_paginate .paginate_button {
  font-weight: 700 !important;
  color: #ffffff !important; /* Color del texto de los botones de paginación */
  background-color: #b5ddb5 !important; /* Color de fondo de los botones de paginación */
  border: 1px solid #ccc !important; /* Borde de los botones de paginación */
  border-radius: 1rem !important;
  cursor: pointer !important;
}
.dark-mode .dataTables_info {
  font-weight: 700 !important;
  color: #ffffff !important; /* Color del texto de la información */
}

.header {
  background-color: #333333;
  padding: 1rem 0 0 0;
}

.contenido-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}

.footer {
  background-color: #333333;
  padding: 1rem 0;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding-top: 3rem;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.propiedades thead {
  background-color: #6318ff;
}
table.propiedades thead th {
  color: #FFFFFF;
  padding: 2rem;
}
table.propiedades td {
  background-color: #d5ceff;
  color: #1a1b15;
  padding: 0.5rem;
  font-weight: 700;
}

.tabla-container {
  border-radius: 1rem;
  background-color: #d5ceff;
  color: #FFFFFF;
  display: block;
  padding: 2rem 0;
  margin: 0 auto;
  width: 80%;
  overflow-x: auto; /* Habilita el desplazamiento horizontal */
  overflow-y: auto; /* Habilita el desplazamiento vertical */
  max-height: 80%; /* Altura máxima del contenedor antes de que aparezca la barra de desplazamiento vertical */
}
.tabla-container label, .tabla-container input {
  color: #1a1b15;
  padding-right: 2rem;
}

.table {
  width: 100%;
  border-collapse: collapse;
  border: 1px solid #ddd; /* Borde alrededor de la tabla */
}

.table th, .table td {
  padding: 1px;
  text-align: left;
  border: 1px solid #ddd; /* Borde alrededor de las celdas */
}

/* Estilo para los botones de paginación de DataTables */
.dataTables_paginate .paginate_button {
  font-weight: 700 !important;
  color: #ffffff !important; /* Color del texto de los botones de paginación */
  background-color: #FFFFFF !important; /* Color de fondo de los botones de paginación */
  border: 3px solid #ccc !important; /* Borde de los botones de paginación */
  border-radius: 1rem !important;
  cursor: pointer !important;
}

/* Estilo para el cuadro de búsqueda */
.dataTables_filter input {
  font-weight: 700 !important;
  margin-bottom: 2rem;
}

/* Estilo para el label asociado al cuadro de búsqueda */
.dataTables_filter label {
  font-weight: 700 !important;
  margin-bottom: 2rem;
}

/* Estilo para el texto de la información de DataTables (ejemplo: "Showing 1 to 10 of 20 entries") */
.dataTables_info {
  font-weight: 700 !important;
  color: #1a1b15 !important;
  padding-left: 2rem;
}

.alerta {
  display: block;
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem auto;
  width: 70%;
  border-radius: 1rem;
}
.alerta.error {
  background-color: #cb0000;
}
.alerta.exito {
  background-color: #329f00;
}

