  background-color: #cbcbcb;
  border: 1px solid #cbcbcb;
  border-radius: 1rem;
  font-weight: 550;
}
.formulario textarea {
  height: 20rem;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
